.homepage {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  margin-top: 0 !important;
  align-items: center;
  justify-content: center;
}

.homepage a {
  position: absolute;
  margin: 0 auto;
  bottom: 2rem;
}

.servers {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 90vh;
    align-items: center;
}
thead tr th{
    background-color: #7a1212 !important;
    color: white !important;
}

tbody tr{
    cursor: pointer;
    border: none !important;
}

tbody tr td{
    border-bottom: none !important;
}
.bottomnavbar {
    position: fixed;
    width: 100%;
    height: 62px !important;
    bottom: 0 !important;
    z-index: 999 !important;
    border-top: 2px solid var(--vt-primary);
}

.bottomnavbar svg {
    font-size: 1rem;
    padding: .5rem;
}
.MuiCard-root {
    margin-bottom: 16px;
}
